  float: left;
  margin: 120px 50px 20px 0;
}

h2[_v-f6e88e90]{
  font-size: 40px;
  color: #fff;
  margin-bottom: 10px;
}

.hello ul h2[_v-f6e88e90] {
  -webkit-transform: translate(650px,0px);
          transform: translate(650px,0px);
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  opacity: 0;
}
/*动画执行时的样式*/
.hello:hover ul h2[_v-f6e88e90]{
  -webkit-transform: translate(0px,0px);
          transform: translate(0px,0px);
  opacity: 1;
}


ul[_v-f6e88e90]{
  margin: 160px 0 90px 0;
  display: inline-block;
}
/*照片动画*/
.pic[_v-f6e88e90]{
  display: block;
  float: left;
  height: 680px;
  width: 500px;
  margin-right: 50px;
  -webkit-animation: pic 3s ease-in-out backwards;
          animation: pic 3s ease-in-out backwards;
}
@-webkit-keyframes pic{
  0% { 
    -webkit-transform: translateY(-200px); 
            transform: translateY(-200px);
    opacity: 0;
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
@keyframes pic{
  0% { 
    -webkit-transform: translateY(-200px); 
            transform: translateY(-200px);
    opacity: 0;
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

/*个人介绍动画开始样式*/
.hello ul li[_v-f6e88e90] {
  -webkit-transform: translate(650px,0px);
          transform: translate(650px,0px);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  opacity: 0;
}
/*动画执行时的样式*/
.hello:hover ul li[_v-f6e88e90]{
  -webkit-transform: translate(0px,0px);
          transform: translate(0px,0px);
  opacity: 1;
}
/*动画依次延时*/
.hello ul li[_v-f6e88e90]:nth-of-type(1) {
  -webkit-transition-delay: 0.05s;
          transition-delay: 0.05s;
}
.hello ul li[_v-f6e88e90]:nth-of-type(2) {
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
}
.hello ul li[_v-f6e88e90]:nth-of-type(3) {
