}
.hello ul li[_v-f6e88e90]:nth-of-type(5) {
  -webkit-transition-delay: 0.25s;
          transition-delay: 0.25s;
}
.hello ul li[_v-f6e88e90]:nth-of-type(6) {
  -webkit-transition-delay: 0.25s;
          transition-delay: 0.25s;
}
.hello ul li[_v-f6e88e90]:nth-of-type(7) {
  -webkit-transition-delay: 0.25s;
